// --------------------------------------------------
// Modules: Banner
// --------------------------------------------------

.banner {

    position: relative;
    overflow: hidden;
    width: 100%;
    height: 100vh;

    min-height: 768px;

    .module__wrapper {
        width: 100%;
        height: 100%;
    }
    
    &__content {
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        z-index: 10;
        @extend %mlr-small;
        @extend %ptb-small;
        width: 100%;
    }

    &__footer {
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 10;
        @extend %plr-small;
        @extend %ptb-small;
    }

    &__scroll {

        position: absolute;
        bottom: 0;
        right: 0;
        z-index: 10;

        @extend %pb-small;
        @extend %pr-medium;

        display: flex;
        align-items: center;
        column-gap: 30px;

        @include down(md) {
            display: none;
        }

        p {
            letter-spacing: 0.2em;
        }

    }
    
}