// --------------------------------------------------
// Modules: Banner: Content
// --------------------------------------------------

.banner__content {    

    .new__text {
        opacity: 0;
    }

    .rotate__phrases, .new__phrases {
        
        position: absolute;
        top: 50%;
        max-width: 100%;
        transform: translateY(-50%);
        left: 0;
        text-align: left;
        z-index: 10;

        @include up(md) {
            max-width: 50%;
        }

        @include up(xl) {
            max-width: 50%;
        }

        p {
            
            line-height: 0.9em;
            font-weight: bold;
            letter-spacing: -1px;

            font-size: 70px;

            @include up(md) {
                font-size: 100px;
            }

            @include up(lg) {
                font-size: 80px;
            }

            @include up(xl) {
                font-size: 120px;
            }

        }

        strong {
            position: relative;
            color: var(--color-primary);
            line-height: 1em;
            // display: inline-block;
            // height: 100%;
            overflow: hidden;
        }

    }
    
    .split-chars {

        position: relative;

       span {
            opacity: 0;
            // transform: translateX(-20px);
            position: relative;
            display: inline-block;
       }

        .chars_type{
            width: 1px;
            height: 100%;
            background-color: white;
            position: absolute;
            left: 0;
            top: 0;
            opacity: 0;
        }       

    }

    // space
    strong span .split-chars span {
        opacity: 0 !important;
    }

    .highlight-rect {
        position: absolute;
        left: 0;
        top: 0;
        background-color: var(--color-primary); /* Color de fondo del rectángulo */
        z-index: -1; /* Asegúrate de que esté detrás del texto */
        pointer-events: none; /* Para evitar que interfiera con la interacción del usuario */
        transform-origin: left center; /* Establecer el punto de origen para la expansión */
    }

}
