// --------------------------------------------------
//	Modules: Banner: Background
// --------------------------------------------------

.banner__bg {

    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 5;

    .media {
        width: 100%;
        height: 100%;
    }

    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        z-index: 6;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.6);
    }

    video,
    img {
        height: 100%;
        width: 100%;
        object-fit: cover;
    }
    
}