.footer__contact {
    background: none;
    padding: 0;
    margin: 0;

    height: 10vh;

    .footer__legal {
        background-color: #4a4a4a;
        height: 100%;
        @include down(md) {
            background-color: var(--color-black);
        }

        nav {
            height: 100%;
        }
        ul {
            height: 100%;
            display: flex;
            align-items: center;
            padding: 0;
            @include down(md) {
                padding: $m_xl 0;
            }
        }
    }
}

.contact {
    position: relative;
    min-height: calc(100vh - 10vh);
    display: flex;

    &__start {
        position: relative;
        display: flex;
        flex-direction: column;
        width: 100%;

        .contact__title {
            padding-bottom: $s_xs;
            border-bottom: 1px solid #b2b2b2;

            h1 {
                @extend %fs3;
                text-transform: uppercase;
                font-weight: 700;
            }
        }

        .contact__cta {
            @extend %plr-medium;
            padding-bottom: $d_xxl;

            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            flex: 1 1 0;

            gap: $s_xl;

            @include down(md) {
                padding-top: $m_xxl;
            }

            &-content {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                text-align: center;

                @include down(md) {
                    padding-bottom: $m_xxl;
                }

                p {
                    @extend %fs3;
                    font-weight: 300;
                    line-height: 1.6em;
                }
            }
        }
    }

    &__modal {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        z-index: 999;

        background-color: var(--color-black);

        opacity: 0;
        display: none;

        // * {
        //     color: var(--color-white);
        // }

        &-slider {
            height: 100%;
            .slide {
                width: 100%;
                height: 100%;

                display: flex;
                justify-content: center;

                &__wrapper {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    flex-direction: column;
                    gap: $s_xl;

                    width: $max-width-medium;
                    height: 100%;

                    @include down(md) {
                        width: 100%;
                        padding: 0 $m_sm;
                    }
                }

                &__title {
                    @extend %fs3;
                    font-weight: 300;
                    line-height: 1.6em;
                    text-align: center;
                }

                &__options {
                    display: grid;
                    grid-template-columns: 1fr;
                    grid-template-rows: 1fr;

                    gap: 10px;

                    width: 100%;

                    @include up(md) {
                        grid-template-columns: 1fr 1fr 1fr;
                        grid-template-rows: 100px 100px;
                    }

                    .slide__item {
                        color: var(--color-white);
                        background: transparent;
                        border: 1px solid #464646;
                        border-radius: 9px;
                        @extend %fs4;

                        @include down(md) {
                            padding: $m_lg;
                        }

                        &:hover {
                            background-color: var(--color-grey);
                        }
                    }
                }

                &__step {
                    border: 1px solid var(--color-white);
                    border-radius: 100%;
                    width: 64px;
                    aspect-ratio: 1 / 1;

                    display: flex;
                    justify-content: center;
                    align-items: center;

                    span {
                        @extend %fs6;
                    }
                }
            }

            .form {
                .slide__wrapper {
                    width: $max-width-small;
                    gap: $s_lg;

                    @include down(md) {
                        width: 100%;
                        padding: 0 $m_sm;
                    }

                    .wpcf7 {
                        width: 100%;
                        p {
                            display: flex;
                            flex-direction: column;
                            align-items: center;
                        }
                        &-form-control-wrap {
                            width: 100%;
                        }
                        &-submit {
                            margin: 0 auto;
                            margin-top: $d_lg;
                        }
                    }
                }
            }
        }

        .btn--close,
        .btn--prev {
            background: none;
            outline: none;
            border: none;
            cursor: pointer;

            width: 38px;
            aspect-ratio: 1 / 1;
        }

        .btn--prev {
            position: absolute;
            top: $s_lg;
            left: $s_lg;
            z-index: 999;
            visibility: hidden;
        }

        .btn--close {
            position: absolute;
            top: $s_lg;
            right: $s_lg;
            z-index: 999;
        }
    }
}
