.steps {
    .module__wrapper {
        @extend %plr-large;
        @extend %ptb-large;
        display: flex;
        flex-direction: column;
        row-gap: $m_lg;

        @include up(md) {
            flex-direction: row;
            column-gap: $d_lg;
        }

        .steps__end,
        .steps__start {
            width: 50%;

            @include down(md) {
                width: 100%;
            }

            a {
                @extend %fs6;
                padding: 15px 20px;
            }
        }

        .steps__start {
            @include up(md) {
                position: sticky;
                top: 0;
                height: fit-content;
            }

            p {
                @extend %fs1;
                font-weight: 300;
                line-height: 0.9em;

                margin-bottom: $d_xl;
                @extend %pt-medium;

                @include down(md) {
                    line-height: 1.1em;
                }
            }

            a {
                @include down(md) {
                    display: none;
                }
            }
        }

        .steps__end {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: $s_xs;

            .steps__item {

                width: 22vw;
                padding: $d_lg $d_md $d_lg $d_md;
                border: 1px solid #464646;
                border-radius: 8px;

                display: flex;

                opacity: 0;
                transform: translateY(40px);

                position: relative;
                overflow: hidden;

                @include down(md) {
                    width: 100%;
                    height: 150px;
                    padding: $m_lg $m_lg $m_xxl $m_lg;
                }

                p {
                    position: relative;
                    z-index: 5;
                }

                p:first-of-type {
                    @extend %fs6;
                    color: #48a4c7;
                }
                p:last-of-type {
                    @extend %fs4;
                    line-height: 1.2em;
                    padding-top: $s_sm;
                    padding-left: $s_sm;
                    @include up(xl) {
                        font-size: 30px;
                    }
                    @include up(xxxl) {
                        font-size: 36px;
                    }
                }

                img {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    z-index: 1;

                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }

            a {
                margin-top: $m_xxl;
                @include up(md) {
                    display: none;
                }
            }
        }
    }
}
