// --------------------------------------------------
//	Modules: Clients
// --------------------------------------------------

.clients {

    @extend %ptb-large;

    &__header {
        @extend %mlr-medium;
        @extend %mb-small;
        padding-bottom: $s_sm;
        border-bottom: 1px solid #b2b2b2;

        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__title {
        @extend %fs3;
        font-weight: 700;
        text-transform: uppercase;
    }

    &__slider {

        @extend %ml-medium;

        .splide__slide .client {
            padding: $s_lg $s_md;
            border: 1px solid #464646;
            // width: 400px;
            height: 100%;
            // aspect-ratio: 1 / 1;
            border-radius: 10px;

            img {
                margin-bottom: $s_lg;
            }

            .client__author {
                font-weight: 400;
                color: var(--color-grey-light);
                padding-bottom: $s_xs;
            }

            .client__text {
                font-style: italic;
            }
            
        }

    }

}
