// --------------------------------------------------
//	Modules: Awards
// --------------------------------------------------

.awards {
    // display: none;

    @extend %plr-medium;
    @extend %pt-large;
    @extend %pb-extralarge;
    position: relative;  
    
    overflow: hidden;

    .award__title {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translateX(-50%);
        font-size: 400px;
        font-weight: bold;
        z-index: 0;
        opacity: 0.05;
    }

    .awards__items {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        position: relative;
        z-index: 10;
        gap: 70px;
        @include up(md) {
            column-gap: 80px;
            row-gap: 100px;
        }
        @include up(lg) {
            column-gap: 60px;
            row-gap: 150px;
        }
        @include up(xl) {
            column-gap: 45px;
            row-gap: 150px;
        }
        @include up(xxxl) {
            gap: 150px;
        }
    }

    .card {
        width: 100%;
        @include up(md) {
            width: calc(50% - 40px);
        }
        @include up(lg) {
            width: 38%;
        }
        @include up(xl) {
            width: calc(33.33% - 30px);
        }
        @include up(xxxl) {
            width: calc(33.33% - 150px);
        }
    }
}
