// --------------------------------------------------
//	Modules: Quotes
// --------------------------------------------------

.quotes {

    position: relative;
    overflow: hidden;
    
    height: 100vh;
    height: calc(var(--vh) * 100);

    min-height: 768px;
    overflow: hidden;

    display: flex;
    align-items: center;
    justify-content: center;

    .module__wrapper {        
        @extend %ptb-large;
        // @extend %plr-large;
    }

    &__wrap {
        position: relative;
    }
    

    &__items {
        position: relative;
        z-index: 50;        
        // max-width: 720px;
        // margin: 0 auto;
        margin-top: $s_xxl;
        // overflow: hidden;
        // display: flex;
        width: 100%;
    }

    &__item {

        // flex: 0 0 auto;
        width: 100vw;
        display: flex;
        gap: 20px;
        padding: 0 $m_md;

        max-width: 720px;
        // margin: 0 auto;

        // position: absolute;
        // top: 50%;
        // left: 50%;
        // transform: translate(-50%,-50%);

        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        opacity: 0; /* Inicia invisible */

        // opacity: 0;
        // transform: translateY(100);

        .quotes__number {
            color: var(--color-secondary);
            @extend %fs4;
        }

        p {
            @extend %fs4;
            text-align: center;
            @include up(xl) {
                font-size: 32px;
            }
            @include up(xxxl) {
                font-size: 36px;
            }
        }

    }    

}