.banner_parallax {

    .module__wrapper {

        position: relative;
        overflow-y: hidden;
        overflow-x: hidden;
        height: 100vh;

        .media {
            img,
            video {
                animation: imageZoomAnimation 24s linear infinite 0s;
                height: 100%;
                // object-fit: contain;

                @include up(md) {
                    height: unset;
                    width: 100%;
                    object-fit: cover;
                }
            }
        }

        .banner_parallax-content {

            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 10;

            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            svg {
                margin-bottom: $s_md;
                width: 40vw;

                @include down(md) {
                    width: 80vw;
                }
            }

            p {
                @extend %fs2;
                font-weight: 300;
            }
        }        

    }

    .banner__scroll {
        @extend %pb-large;
    }
    
}

// image animation (zoom)
@keyframes imageZoomAnimation {
    0% {
        transform: scale(1);
        -ms-transform: scale(1);
    }
    30% {
        transform: scale(1.1);
        -ms-transform: scale(1.1);
    }
    100% {
        transform: scale(1);
        -ms-transformm: scale(1);
    }
}