// --------------------------------------------------
//	Modules: Words
// --------------------------------------------------

.words {

    position: relative;
    overflow: hidden;    

    .module__wrapper {
        @extend %ptb-large;
    }

    &__items {
        margin-top: $s_xl;
        display: flex;
        text-align: center;
    }

    &__item {             
        width: 100vw;
        flex: 0 0 auto;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__title {
        font-size: 75px;
        font-weight: bold;   
        @include up(md) {
            font-size: 125px;
        }
        @include up(xl) {
            font-size: 200px;
        }
    }

    &__text {        
        width: 80%;
        max-width: 1000px;
        margin: 0 auto;
        p {
            @extend %fs3;
        }
        strong {
            font-weight: bold;   
        }
    }

}