// --------------------------------------------------
//	Modules: Cases
// --------------------------------------------------

.cases {
    
    display: flex;
    flex-direction: column;
    width: 100%;
    position: relative;
    z-index: 1;

    overflow: hidden;

    .cases_list {
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    .case {
        display: flex;
        width: 100vw;
        height: 100vh;
        position: relative;
        overflow: hidden;
        text-decoration: none;
    }

    .case__bg {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        left: 0;
        top: 0;
        width: 100vw;
        height: 100vh;
        background-position: center;
        will-change: transform;
        background-size: cover;

        will-change: transform, opacity;
    }

    .case__text {

        flex-direction: column;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        display: flex;
        text-align: center;
        width: 95%;
        padding: 3rem;
        margin: 0 auto;
        box-sizing: border-box;
        // max-width: 40rem;w
        position: relative;
        z-index: 100;
        align-items: center; 
        
        will-change: transform, opacity;
    
        p {
            @extend %fs1;
            line-height: 1.25em;
            font-weight: bold;
            color: var(--color-white);
        }

    }

}