// --------------------------------------------------
//	Modules: Hero
// --------------------------------------------------

.hero {

    position: relative;
    overflow: hidden;
    width: 100%;      

    .module__wrapper {
        height: 100vh;
        min-height: 768px;
        display: flex;
        justify-content: center;
        align-items: center;    
    }

    .hero__title {
        @extend %fs1;
        color: var(--color-white);
        line-height: 1.2em;
        text-align: center;
        font-weight: 700;
        max-width: min(1460px, 75vw);
        margin-bottom: $s_lg;
    }

    .hero__text p {
        @extend %fs1;
        color: var(--color-white);
        line-height: 1.2em;
        text-align: center;
        font-weight: 300;

        max-width: min(1460px, 75vw);

        font-size: 40px;

        @include up(md) {
            font-size: 55px;
        }
        @include up(lg) {
            font-size: 50px;
        }
        @include up(xl) {
            font-size: 66px;
        }

        .char {
            opacity: 0.3;
        }
    }

    .hero__title + .hero__text {
        max-width: 1200px;
        margin: 0 auto;
        p {
            @extend %fs3;
        }
    }
}
