// --------------------------------------------------
//	Modules: Services
// --------------------------------------------------

.services {

    position: relative;
    overflow: hidden;
    width: 100%;  
    @extend %mb-large;

    &__desktop {
        @include down(lg) {
            display: none;
        }
    }

    &__mobile {
        @include up(lg) {
            display: none;
        }
    }

    // all images
    &__images {               
        @include up(lg) {
            padding-left: 50vw;
        }
    }

    // image wrap
    .services__image {
        @include down(lg) {
            padding: 0 $s_md;
        }  
    }

    // image
    img {
        width: 100%;
        height: auto;              
        @include up(lg) {
            width: 50vw;
            height: 100vh;
            object-fit: cover;
            object-position: top;
        }
    }

    // wrap contents
    .services__content {
        position: absolute;
        width: 50vw;
        height: 100vh;
        top: 0;
        left: 0;
        @extend %pl-medium;
        @extend %pb-small;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    // title
    &__title {
        @extend %fs3;
        font-weight: bold;
        text-transform: uppercase;    
        &:before {
            content: '';
            display: block;
            height: 1px;
            width: 100%;
            background: var(--color-white);
            margin-bottom: $s_xl;
        }
    }

    // texts
    .services__texts {
        position: relative;
        width: 100%;
    }

    // text items
    .service__text {     

        @include down(lg) {
            padding: $s_xxl $s_md;
        }
        @include up(lg) {
            position: absolute;
            bottom: 0;
            left: 0;
            opacity: 0;        
            width: 70%;
            transition: all 0.3s;
        }

        &.active {
            opacity: 1;
            .service__title .split-chars {
                transition: all 0.6s ease-out;
                transform: translateY(0) !important;
            }
            .service__body {
                transition-delay: 0.5s;
                opacity: 1;
            }
        }

        .service__title p {
            @extend %fs3;
            line-height: 1;
            font-weight: bold;
            text-transform: uppercase;
            margin-bottom: $s_sm;
        }

        .service__body {
            transition: all 0.5s;
            @include up(lg) {
                opacity: 0;
            }
        }

    }

    .split-words {
        overflow: hidden;
    }

}