// --------------------------------------------------
//	Modules: Blocks
// --------------------------------------------------

.blocks {

    position: relative;
    overflow: hidden;
    width: 100%;

    &__items {
        height: 100%;
        display: flex;
        flex-direction: column;
        @include up(lg) {
            flex-direction: row;
            height: min(750px, 68vh);
        }
    }

    &__item {

        position: relative;
        overflow: hidden;
        height: 400px;
        border: 1px solid white;
        z-index: 10;
        padding: 25px;

        @include up(lg) {
            height: 100%;
            flex: 1 1 0;
        }

        video {

            position: absolute;
            object-fit: contain;
            height: 100%;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
            z-index: 1;
            filter: brightness(0.5);
            opacity: 0;

            // &::after {
            //     content: '';
            //     position: absolute;
            //     top: 0;
            //     left: 0;
            //     z-index: 2;
            //     width: 100%;
            //     height: 100%;
            //     background: rgba(0, 0, 0, 0.9);
            // }
        }

        .blocks__overlay {
            
            position: relative;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 5;

            display: flex;
            justify-content: center;

            .blocks__title {

                position: absolute;
                top: 40%;
                left: $d_xl;
                z-index: 7;
                margin: 0 auto;

                padding: 0 $s_sm;

                p {
                    color: var(--color-white);
                }

                p:first-of-type {
                    @extend %fs6;
                    position: absolute;
                    top: 0;
                    left: -40px;
                    padding-top: 7px;
                }

                p:last-of-type {
                    @extend %fs3;
                    line-height: 1.2em;
                    @media screen and (min-width: 992px) and (max-width: 1399px) {
                        font-size: 32px !important;
                    }
                }

            }

            .blocks__text {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: $d_xl;
                z-index: 6;

                padding: 0 $s_sm;

                opacity: 0;

                p:last-of-type {
                    @extend %fs6;
                    line-height: 1.2em;
                    color: var(--color-white);
                }
            }

            .blocks__arrow {
                position: absolute;
                bottom: 60px;
                left: $d_xl;
                z-index: 5;

                display: flex;
                align-items: center;

                svg {
                    width: 60px;
                    height: auto;
                    @include up(md) {
                        width: 70px;
                    }
                    @include up(lg) {
                        width: 50px;
                    }
                    @include up(xxl) {
                        width: 60px;
                    }
                }
            }
        }

        .block__hover {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 99;
        }
    }
}
