// --------------------------------------------------
//	Modules: Values
// --------------------------------------------------

.values {
    position: relative;
    overflow: hidden;
    height: 100%;

    @extend %ptb-large;
    @extend %plr-medium;

    .module__wrapper {
        height: 100%;

        .values__foreground {
            position: relative;
            z-index: 10;

            display: flex;
            justify-content: flex-start;
            align-items: center;
            flex-direction: column;

            .values__header p {
                @extend %fs1;
                font-weight: 700;
                line-height: 0.8em;

                margin-bottom: $s_xxl;
            }

            .values__main {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                align-items: center;

                row-gap: 5vh;

                @include up(md) {
                    column-gap: 13vw;
                    max-width: $max-width-large;
                    flex-direction: row;
                }

                .values__start,
                .values__end {
                    width: 100%;

                    @include up(md) {
                        width: 50%;
                    }
                }

                .values__start {
                    display: flex;
                    flex-direction: column;
                    gap: $s_md;

                    .values__item {

                        border-radius: 8px;
                        backdrop-filter: blur(6px);
                        background-color: rgba(255, 255, 255, 0.2);

                        display: flex;
                        align-items: center;

                        padding: $s_md;

                        .values__count {

                            font-size: 50px;
                            font-weight: 300;
                            width: 60%;

                            @include up(md) {
                                font-size: 70px;
                            }

                        }

                        .values__el {
                            width: 40%;
                        }
                    }
                }

                .values__end {
                    .values__body {
                        padding-bottom: $s_md;
                        p {
                            padding-bottom: $s_md;
                        }
                    }

                    a {
                        @include down(md) {
                            margin: 0 auto;
                        }
                    }
                }
            }
        }
    }
}
